"use client";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { ScrollArea } from "@/components/ui/scroll-area";
import { BeforeAfterSlider } from "./BeforeAfterSlider";
import { Loader2, Info } from "lucide-react";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from "@/components/ui/dialog";
// Our catalog of historical images
const historicalImages = [
  {
    id: 1,
    title: "Temple of Sufes",
    figure: "Fig. 85",
    period: "2nd Century AD",
    description:
      "Main temple complex showing Corinthian columns and detailed architectural elements. The structure demonstrates typical Roman provincial architecture with local limestone construction.",
    bwImage: "/fig_325/before.jpg",
    colorImage: "/fig_325/after.jpg",
    bwenImage:"/enhanced/fig_325/before.jpeg",
    colorenhancedImage:"/enhanced/fig_325/after.jpeg",
    location: "Northern Quarter",
    category: "Architecture",
    documentation: "AAT2 Atlas, Page 156",
  },
  
  {
    id: 2,
    title: "Olive Oil Press",
    figure: "Fig. 92",
    period: "3rd Century AD",
    description:
      "Industrial olive oil production facility with preserved mechanical elements. Shows the economic importance of olive oil production in ancient Sufes.",
    bwImage: "/fig_296/before.jpg",
    colorImage: "/fig_296/after.jpg",
    bwenImage:"public/enhanced/fig_296/before.jpeg",
    colorenhancedImage:"public/enhanced/fig_296/after.jpeg",
    location: "Industrial District",
    category: "Industrial",
    documentation: "AAT2 Atlas, Page 178",
  },
];

export default function Colorization() {
  const [selectedImage, setSelectedImage] = useState<(typeof historicalImages)[0] | null>(null);
const [isProcessing, setIsProcessing] = useState(false);
const [progress, setProgress] = useState(0);
const [isEnhancing, setIsEnhancing] = useState(false);
const [isEnhanced, setIsEnhanced] = useState(false); // Tracks if enhancement is done

const handleEnhance = () => {
  setIsEnhancing(true);
  setProgress(0);

  const interval = setInterval(() => {
    setProgress((prev) => {
      if (prev >= 100) {
        clearInterval(interval);
        setIsEnhancing(false);
        setIsEnhanced(true); // Mark enhancement as done
        return 100;
      }
      return prev + 2;
    });
  }, 50);
};


  // Function to handle image selection
  const handleImageSelect = (image: (typeof historicalImages)[0]) => {
    setSelectedImage(image);
    setIsProcessing(true);
    setProgress(0);

    const interval = setInterval(() => {
      setProgress((prev) => {
        if (prev >= 100) {
          clearInterval(interval);
          setIsProcessing(false);
          return 100;
        }
        return prev + 2;
      });
    }, 50);
  };
  return (
    <div className="container mx-auto p-4 space-y-8">
      <div className="text-center space-y-4 mb-12">
        <h1 className="text-4xl font-serif text-amber-100">
          Historical Image Colorization
        </h1>
        <p className="text-lg text-amber-200/80 max-w-2xl mx-auto">
          Discover Sbeitla's artifacts in vivid color through AI-powered
          enhancement
        </p>
      </div>

      {/* Image Gallery */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {historicalImages.map((image) => (
          <Card
            key={image.id}
            className="group bg-stone-900/50 border-amber-900/20 hover:bg-stone-900/80 transition-all duration-500 cursor-pointer"
            onClick={() => handleImageSelect(image)}
          >
            <div className="p-4 space-y-4">
              <div className="aspect-[4/3] overflow-hidden rounded-lg">
                <img
                  src={image.bwImage}
                  alt={image.title}
                  className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-500"
                />
              </div>
              <div className="space-y-2">
                <div className="flex items-start justify-between gap-4">
                  <h3 className="text-lg font-serif text-amber-100">
                    {image.title}
                  </h3>
                  <span className="text-sm text-amber-500 font-mono">
                    {image.figure}
                  </span>
                </div>
                <p className="text-sm text-amber-200/60 line-clamp-2">
                  {image.description}
                </p>
                <div className="flex items-center justify-between text-xs text-amber-200/40">
                  <span>{image.period}</span>
                  <span>{image.category}</span>
                </div>
              </div>
            </div>
          </Card>
        ))}
      </div>

      {/* Colorization Modal */}
      <Dialog
        open={!!selectedImage}
        onOpenChange={() => setSelectedImage(null)}
      >
        <DialogContent className="max-w-5xl bg-stone-950 border-amber-900/20">
          <DialogHeader>
            <DialogTitle className="text-2xl font-serif text-amber-100">
              {selectedImage?.title}
            </DialogTitle>
            <DialogDescription className="text-amber-200/60 font-mono">
              {selectedImage?.figure} • {selectedImage?.period}
            </DialogDescription>
          </DialogHeader>

          <div className="grid md:grid-cols-[2fr,1fr] gap-6">
  {/* Image Display Section */}
  <div className="space-y-4">
    {isProcessing || isEnhancing ? (
      // Loading Animation during processing/enhancement
      <div className="aspect-[4/3] bg-stone-900/50 rounded-lg flex items-center justify-center">
        <div className="text-center space-y-4">
          <Loader2 className="w-10 h-10 text-amber-500 animate-spin mx-auto" />
          <div className="space-y-2 px-8">
            <div className="text-amber-100">
              {isProcessing ? "Image Colorization..." : "Enhancing Image..."}
            </div>
            <div className="h-1 bg-stone-800 rounded-full overflow-hidden">
              <div
                className="h-full bg-amber-600 transition-all duration-300"
                style={{ width: `${progress}%` }}
              />
            </div>
            <div className="text-sm text-amber-200/60">{progress}% Complete</div>
          </div>
        </div>
      </div>
    ) : (
      selectedImage && (
        <BeforeAfterSlider
          beforeImage={isEnhanced ? selectedImage.bwImage : selectedImage.bwenImage}
          afterImage={isEnhanced ? selectedImage.colorImage : selectedImage.colorenhancedImage}
          className="border border-amber-900/20 rounded-lg overflow-hidden"
        />
      )
    )}
  </div>

  {/* Details Section */}
  {selectedImage && (
    <div className="space-y-6">
      <p className="text-sm text-amber-200/60">{selectedImage.description}</p>

      <Card className="bg-stone-900/50 border-amber-900/20 p-4">
        <div className="space-y-3 text-sm">
          <div className="flex justify-between">
            <span className="text-amber-200/60">Period</span>
            <span className="text-amber-100">{selectedImage.period}</span>
          </div>
          <div className="flex justify-between">
            <span className="text-amber-200/60">Location</span>
            <span className="text-amber-100">{selectedImage.location}</span>
          </div>
          <div className="flex justify-between">
            <span className="text-amber-200/60">Category</span>
            <span className="text-amber-100">{selectedImage.category}</span>
          </div>
          <div className="flex justify-between">
            <span className="text-amber-200/60">Reference</span>
            <span className="text-amber-100">{selectedImage.documentation}</span>
          </div>
        </div>
      </Card>

      <div className="flex items-center gap-2 text-xs text-amber-200/40">
        <Info className="w-4 h-4" />
        <span>
          Drag the slider to compare original and enhanced versions
        </span>
      </div>

      {/* Enhance Quality Button */}
      <div className="flex justify-center mt-4">
        <button
          onClick={handleEnhance}
          className="bg-amber-600 text-white px-4 py-2 rounded-md hover:bg-amber-700 transition"
          disabled={isEnhancing} // Disable button while enhancing
        >
          Enhance Quality
        </button>
      </div>
    </div>
  )}
</div>

        </DialogContent>
      </Dialog>
    </div>
    
  );
}
